import React from 'react';
import connect from '@vkontakte/vk-bridge';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import { Icon28Story } from '@vkontakte/icons';
import { Icon24Repost } from '@vkontakte/icons';
import { View, Alert, Link, Snackbar, Avatar, ScreenSpinner, ModalPageHeader, ActionSheet, ActionSheetItem, ModalRoot, ModalPage, FormLayoutGroup, FormLayout, Input, Div, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Home from './panels/Home';
import Setting from './panels/Setting';
import './panels/Persik.css';
import Icon16Clear from '@vkontakte/icons/dist/16/clear';
import { Icon16CancelCircle } from '@vkontakte/icons';
const queryString = require('query-string');
const parsedHash = queryString.parse(window.location.hash);
const blueBackground = {
  backgroundColor: 'var(--accent)'
};
class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activePanel: 'home',
      count: "‚Ä¢‚Ä¢‚Ä¢",
      popout: null,
      snackbar: null,
      fetching: false
    };
    this.closeModal = this.closeModal.bind(this);
    this.openBase = this.openBase.bind(this);
    this.openError = this.openError.bind(this);
    this.newAds = this.newAds.bind(this);
    this.checkBonuseAd = this.checkBonuseAd.bind(this);
  }

  componentDidMount() {
    this.setState({ popout: <ScreenSpinner /> });
    connect.subscribe((e) => {
      // this.setState({ popout: <ScreenSpinner /> });
      // setTimeout(() => { this.setState({ popout: null }) }, 2000);
      // window.addEventListener('popstate', e => e.preventDefault() & this.menu(e));
      // window.history.pushState({ panel: 'home' }, `home`);
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
          this.setState({ fetchedUser: e.detail.data });
          this.auth();
          break;
        default:
        // console.log(e.detail.type);
        // console.log(e.detail.data);
      }

    });
    connect.send('VKWebAppGetUserInfo', {});
    // console.clear();
  }
  setActiveTab = (value) => {
    this.setState({ activeTabTop: `${value}` });
  }
  setActiveTabBanner = (value) => {
    this.setState({ activeTabTopBanner: `${value}` });
  }
  closeModal() {
    this.setState({ activeModal: null });
  }
  modal = (e) => {
    this.setState({ activeModal: e.currentTarget.dataset.to })
  };
  closePopout() {
    this.setState({ popout: null });
  }
  openBase(text = "success") {
    this.setState({ snackbar: null });
    this.setState({
      snackbar:
        <Snackbar
          layout="vertical"
          onClose={() => this.setState({ snackbar: null })}
          before={<Avatar size={24} style={blueBackground}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
        >
          {text}
        </Snackbar>
    });
  }
  openError(text = "error") {
    this.setState({ snackbar: null });
    // if (this.state.snackbar) return;
    this.setState({
      snackbar:
        <Snackbar
          layout="vertical"
          onClose={() => this.setState({ snackbar: null })}
          before={<Avatar size={24} style={blueBackground}><Icon16Clear fill="#fff" width={14} height={14} /></Avatar>}
        >
          {text}
        </Snackbar>
    });
  }


  auth = (popout = 1) => {
    if (popout == 1) this.setState({ popout: <ScreenSpinner /> });
    fetch(`https://ads.apiuser.ru/api/auth/${window.location.search.substring()}`, {
      method: "POST",
      body: JSON.stringify({
        user: `${this.state.fetchedUser.id}`
      }),
      headers: {
        "content-type": "application/json; charset=UTF-8"
      },
    })
      .then((response) => response.json())
      .then((response) => {
        this.setState({ popout: null });
        //console.log(JSON.stringify(response));
        setTimeout(() => {
          // this.setState({ fetching: false });
        }, 1111);
        if (response.status == "error") {
          this.setState({ popout: null });
          this.setState({
            activePanel: "errorconnect",
            errorDescription: response.message
          })
        } else {
          this.setState({ popout: null, count: response.count });
        }
      })
      .catch((error) => {
        this.internetError()
      })
  }


  newAds = () => {
    fetch(`https://ads.apiuser.ru/api/new/${window.location.search.substring()}`, {
      method: "POST",
      body: JSON.stringify({
        user: `${this.state.fetchedUser.id}`
      }),
      headers: {
        "content-type": "application/json; charset=UTF-8"
      },
    })
      .then((response) => response.json())
      .then((response) => {
        //console.log(JSON.stringify(response));
        setTimeout(() => {
          // this.setState({ fetching: false });
        }, 1111);
        if (response.status == "error") {
          this.setState({ popout: null });
          this.setState({
            activePanel: "errorconnect",
            errorDescription: response.message
          })
        } else {
          this.setState({ popout: null, count: response.count });
          this.openBase(response.message);
        }
      })
      .catch((error) => {
        this.internetError()
      })
  }

  checkBonuseAd(type) {
    this.setState({ popout: <ScreenSpinner /> });
    this.setState({ activeModal: null });
    if (connect.supports("VKWebAppShowNativeAds")) {
      connect.send("VKWebAppShowNativeAds", { ad_format: type }).then((data) => {
        if (data.result) {
          this.setState({ popout: <ScreenSpinner /> });
          fetch(`https://ads.apiuser.ru/api/new/${window.location.search.substring()}`, {
            method: "POST",
            body: JSON.stringify({
              user: `${this.state.fetchedUser.id}`
            }),
            headers: {
              "content-type": "application/json; charset=UTF-8"
            },
          })
            .then((response) => response.json())
            .then((response) => {
              this.setState({ activeModal: null });
              this.setState({ popout: null });
              // this.setState({ popout: null });
              switch (response.status) {
                case 'success': //–æ—à–∏–±–∫–∞
                  this.openBase(response.message);
                  this.setState({ count: response.count });
                  break;
                case 'error': //–∏–≥—Ä–∞ –≤—ã–∫–ª—é—á–µ–Ω–∞
                  this.openError(response.message);
                  break;
              }
              //console.log("auth: "+ JSON.stringify(response));
            })

            .catch((error) => {
              this.internetError()

            })
        } else {
          this.openError("–í—ã –Ω–µ –ø–æ—Å–º–æ—Ç—Ä–µ–ª–∏ —Ä–µ–∫–ª–∞–º—É!");
          this.auth(0)
        }
      }).catch((error) => {
        var error_text = `#${error.error_data.error_code} ‚Äî ${error.error_data.error_reason}`;
        // console.log(error.error_data);
        this.openError(`–ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑. –û—à–∏–±–∫–∞ VK: ${error_text}`);
        this.auth(0)
      });
    } else {
      this.openError("–í–∞—à–∞ –ø–ª–∞—Ç—Ñ–æ—Ä–º–∞ –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç —Ä–µ–∫–ª–∞–º—É. –û–±–Ω–æ–≤–∏—Ç–µ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ –í–ö–æ–Ω—Ç–∞–∫—Ç–µ!");
      this.auth(0)
    }
  }

  internetError() {
    if (this.state.snackbar) return;
    this.setState({
      snackbar:
        <Snackbar
          layout="vertical"
          onClose={() => this.setState({ snackbar: null })}
          before={<Avatar size={24} ><Icon16Done width={14} height={14} /></Avatar>}
        >
          –ò–Ω—Ç–µ—Ä–Ω–µ—Ç-—Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ –ø–æ—Ç–µ—Ä—è–Ω–æ :(
        </Snackbar>
    });
  }
  render() {
    const modal = (
      <ModalRoot
        activeModal={this.state.activeModal}
        onClose={this.closeModal}
      >
      </ModalRoot>
    );
    return (

      <View
        popout={this.state.popout}
        // modal={modal}
        activePanel={this.state.activePanel}>
        <Home
          id='home'
          // modal={this.modal}
          count={this.state.count}
          snackbar={this.state.snackbar}
          newAds={this.newAds}
          checkBonuseAd={this.checkBonuseAd}
          openBase={this.openBase}
          openError={this.openError}
        />
      </View>

    );
  }
}

export default App;